import React, { Component } from 'react';
import Balance from 'components/Balance';
import Confrim from 'components/Confrim';
import 'styles/game.css';
import Memoji_v from 'assets/images/memoji/v.svg';

class Game extends Component {
  render() {
    return (
      <article className="balance_game" id="game"> 
        <div className="pos_center">
          <div className="balance_memoji">
            <img src={Memoji_v} className="memoji" alt=" " />
          </div>
          <Balance />
          <p className="balance_guide">  ,   .<br />
                .</p>
        </div>
        <Confrim />
      </article>
    );
  }
}

export default Game;
